import PropTypes from 'prop-types';
import React, { Component } from 'react';
import {
  Button,
  Container,
  Menu,
  Responsive,
  Segment,
  Visibility,
  Icon
} from 'semantic-ui-react';
import HomepageHeading from '../heading/HomepageHeading';

import './DesktopContainer.css';

class DesktopContainer extends Component {
	state = { activeItem: 'home' }

  	handleItemClick = (e, { name }) => this.setState({ activeItem: name });

	hideFixedMenu = () => this.setState({ fixed: false });
	showFixedMenu = () => this.setState({ fixed: true });

	render() {
	  const { children } = this.props;
	  const { fixed } = this.state;
	  const { activeItem } = this.state;

	  return (
		<Responsive minWidth={Responsive.onlyTablet.minWidth}>
		  <Visibility
				once={false}
				onBottomPassed={this.showFixedMenu}
				onBottomPassedReverse={this.hideFixedMenu}
		  >
			<Segment
			  inverted
			  textAlign='center'
			  vertical
			  style={{ height: '50em', padding: '0.001em 0em' }}
			  className="homepage-header-container"
			>
			  <Menu
				fixed={fixed ? 'top' : null}
				inverted
				pointing
				secondary
				size='large'
				className="homepage-menu"
			  >
				<Container>
				  <Menu.Item
				  	name="home"
					active={activeItem === 'home'}
					onClick={this.handleItemClick}
				  >
					Acasa
				  </Menu.Item>
				  <Menu.Item
				  	name="whatwedo"
					active={activeItem === 'whatwedo'}
					onClick={this.handleItemClick}
				  >
				  	Ce facem
				  </Menu.Item>
				  <Menu.Item
				  	name="whoweare"
					active={activeItem === 'whoweare'}
					onClick={this.handleItemClick}
				  >
				  	Cine suntem
				  </Menu.Item>
				  <Menu.Item
				  	name="contactus"
					active={activeItem === 'contactus'}
					onClick={this.handleItemClick}
				  >
				  	Contacteaza-ne
				  </Menu.Item>
				  <Menu.Item position='right'>
					<Button as='a' inverted={!fixed} color={fixed ? 'teal': 'black'} style={{ marginLeft: '0.5em' }}>
						<Icon name='phone' /> Hai sa ne cunoastem
					</Button>
				  </Menu.Item>
				</Container>
			  </Menu>
			  <HomepageHeading />
			</Segment>
		  </Visibility>

		  {children}
		</Responsive>
	  );
	}
};

DesktopContainer.propTypes = {
	children: PropTypes.node,
};

export default DesktopContainer;



