import PropTypes from 'prop-types';
import React from 'react';
import {
  Button,
  Container,
  Divider,
  Grid,
  Header,
  Image,
  List,
  Segment,
  Icon,
  Card
} from 'semantic-ui-react';

import DesktopContainer from './desktop-container/DesktopContainer';
import MobileContainer from './mobile-container/MobileContainer';
import './HomePage.css';

const ResponsiveContainer = ({ children }) => (
  <div>
    <DesktopContainer>{children}</DesktopContainer>
    <MobileContainer>{children}</MobileContainer>
  </div>
)

ResponsiveContainer.propTypes = {
  children: PropTypes.node,
};

const HomepageLayout = () => (
  <ResponsiveContainer>
    <Segment style={{ padding: '8em 0em' }} vertical>
      <Grid container stackable verticalAlign='middle'>
        <Grid.Row>
          <Grid.Column width={8}>
            <Header as='h3' style={{ fontSize: '2em' }}>
              Aparatura de calitate
            </Header>
            <p style={{ fontSize: '1.33em' }}>
              We can give your company superpowers to do things that they never thought possible.
              Let us delight your customers and empower your needs... through pure data analytics.
            </p>
            <Header as='h3' style={{ fontSize: '2em' }}>
              Servicii profesioniste
            </Header>
            <p style={{ fontSize: '1.33em' }}>
              Yes that's right, you thought it was the stuff of dreams, but even bananas can be
              bioengineered.
            </p>
          </Grid.Column>
          <Grid.Column floated='right' width={6}>
            <Image bordered rounded size='large' className="grid-large-image" />
          </Grid.Column>
        </Grid.Row>
        <Grid.Row>
          <Grid.Column textAlign='center'>
            <Button size='huge'> <Icon name='angle right' /> Afla mai multe</Button>
          </Grid.Column>
        </Grid.Row>
      </Grid>
    </Segment>
    <Segment style={{ padding: '0em' }} vertical>
      <Grid celled='internally' columns='equal' stackable>
        <Grid.Row textAlign='center'>
          <Grid.Column style={{ paddingBottom: '5em', paddingTop: '5em' }}>
            <Header as='h3' style={{ fontSize: '2em' }}>
              "Seriosi, capabili. Sunt foarte multumit!"
            </Header>
            <p style={{ fontSize: '1.33em' }}>Asta spun despre noi</p>
          </Grid.Column>
          <Grid.Column style={{ paddingBottom: '5em', paddingTop: '5em' }}>
            <Header as='h3' style={{ fontSize: '2em' }}>
              "Ar fi trebuit sa apelez la ei de la bun inceput..."
            </Header>
            <p style={{ fontSize: '1.33em' }}>
              <Image avatar className="avatar" />
              Cineva ft important
            </p>
          </Grid.Column>
        </Grid.Row>
      </Grid>
    </Segment>
    <Segment style={{ padding: '8em 0em' }} vertical>
      <Container text>
        <Header as='h3' style={{ fontSize: '2em' }}>
          Cu cine vei sta de vorba
        </Header>
        <p style={{ fontSize: '1.33em' }}>
          Instead of focusing on content creation and hard work, we have learned how to master the
          art of doing nothing by providing massive amounts of whitespace and generic content that
          can seem massive, monolithic and worth your attention.
        </p>
        <Button as='a' size='large'>
          <Icon name='angle right' /> Read More
        </Button>
        <Divider
          as='h4'
          horizontal
          color='teal'
          style={{ margin: '3em 0em', textTransform: 'uppercase' }}
        >
          <a href='#'className='header-divider' >Ce facem</a>
        </Divider>
        <Header as='h3' style={{ fontSize: '2em' }}>
          Serviciile noastre
        </Header>
        <p style={{ fontSize: '1.33em' }}>
          Yes I know you probably disregarded the earlier boasts as non-sequitur filler content, but
          it's really true. It took years of gene splicing and combinatory DNA research, but our
          bananas can really dance.
        </p>
        <Button as='a' size='large'>
          <Icon name='angle right' /> I'm Still Quite Interested
        </Button>
        <Divider
          as='h4'
          className='header'
          horizontal
          style={{ margin: '3em 0em', textTransform: 'uppercase' }}
        >
           <a href='#'className='header-divider' >Legislatura</a>
        </Divider>
        <Header as='h3' style={{ fontSize: '2em' }}>
          Baza legala
        </Header>
        <p style={{ fontSize: '1.33em' }}>
          Niste legi pe aici.
        </p>
        <div className='cards'>
        <Card
          header='Decizia 7'
          meta='privind nu stiu ce'
          description='de ce e important documentul asta'
          extra={<Button color='teal' inverted> <Icon name='download' />Descarca</Button>}
        />
        <Card
          header='Hotararea 8'
          meta='privind nu stiu ce'
          description='de ce e important documentul asta'
          extra={<Button color='teal' inverted> <Icon name='download' />Descarca</Button>}
        />
        <Card
          header='Declaratia 9'
          meta='privind nu stiu ce'
          description='de ce e important documentul asta'
          extra={<Button color='teal' inverted> <Icon name='download' />Descarca</Button>}
        />
        </div>
      </Container>
    </Segment>
    <Segment inverted vertical style={{ padding: '5em 0em' }}>
      <Container>
        <Grid divided inverted stackable>
          <Grid.Row>
            <Grid.Column width={3}>
              <Header inverted as='h4' content='About' />
              <List link inverted>
                <List.Item as='a'>Sitemap</List.Item>
                <List.Item as='a'>Contact Us</List.Item>
                <List.Item as='a'>Religious Ceremonies</List.Item>
                <List.Item as='a'>Gazebo Plans</List.Item>
              </List>
            </Grid.Column>
            <Grid.Column width={3}>
              <Header inverted as='h4' content='Services' />
              <List link inverted>
                <List.Item as='a'>Banana Pre-Order</List.Item>
                <List.Item as='a'>DNA FAQ</List.Item>
                <List.Item as='a'>How To Access</List.Item>
                <List.Item as='a'>Favorite X-Men</List.Item>
              </List>
            </Grid.Column>
            <Grid.Column width={7}>
              <Header as='h4' inverted>
                Footer Header
              </Header>
              <p>
                Ceva pe aici sugestiv si foarte wow
              </p>
            </Grid.Column>
          </Grid.Row>
        </Grid>
      </Container>
    </Segment>
  </ResponsiveContainer>
)

export default HomepageLayout;



